import { OnChanges, OnDestroy, SimpleChange } from '@angular/core';
import { Subscription } from 'rxjs/Subscription';
import { CollectionService, Item, ItemChange } from "./collection.service";
import { ConfigurationService } from './configuration.service';
import { copyChanges } from './copy-changes';

/**
 * @hidden
 */
export abstract class CollectionItemComponent implements Item, OnChanges, OnDestroy {
  protected subscription: Subscription;
  public options: any = {};

  constructor(protected configurationService: ConfigurationService,
              protected collectionService: CollectionService) {
    this.subscription = configurationService.onChange$.subscribe(
      store => {
        this.options = store;
        this.notify();
      }
    );
  }

  public ngOnChanges(changes: {[propertyName: string]: SimpleChange}): void {
    const store = this.configurationService.store;
    copyChanges(changes, store);

    this.configurationService.push(store);
  }

  public ngOnDestroy(): void {
    this.subscription.unsubscribe();
  }

  protected notify(): void {
    this.collectionService.notify(
      new ItemChange(this, this.options)
    );
  }
}



